.header {
  display: flex;
  align-items: center;
  margin: 10px 0 10px;
  padding-left: 10px; }
  .header h3 {
    margin: 0; }
  .header a:hover {
    text-decoration: none; }

.header__title a {
  color: white; }

.header__navigation ul {
  margin: 0; }

.header__navigation ul li {
  margin: 0;
  padding: 5px 10px;
  list-style: none;
  border-radius: 3px;
  transition: 200ms ease-out; }
  .header__navigation ul li:hover {
    background-color: #444; }

.header__navigation a:hover {
  color: #00bc8c; }

.random-planet {
    display: flex;
    min-height: 178px;
    padding: 14px;
    margin-bottom: 14px;
}

.random-planet .planet-image {
    width: 150px;
    height: 150px;
    border-radius: 10px;
    margin-right: 14px;
}

.random-planet .list-group {
    margin: 16px 0 0 16px;
}

.random-planet .list-group-item {
    padding: 4px;
}

.random-planet .list-group-item .term {
    margin-right: 8px;
}

@-webkit-keyframes lds-double-ring {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes lds-double-ring {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@-webkit-keyframes lds-double-ring_reverse {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0); }
  100% {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg); } }

@keyframes lds-double-ring_reverse {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0); }
  100% {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg); } }

.spinner {
  margin: 0 auto;
  display: flex;
  align-items: center; }

.spinner__double-ring {
  position: relative; }

.spinner__double-ring::after,
.spinner__double-ring::before {
  position: absolute;
  content: '';
  width: 160px;
  height: 160px;
  top: 20px;
  left: 20px;
  border-radius: 50%;
  border: 8px solid #000;
  border-color: #606e6e transparent #606e6e transparent;
  -webkit-animation: lds-double-ring 2s linear infinite;
          animation: lds-double-ring 2s linear infinite; }

.spinner__double-ring::after {
  width: 140px;
  height: 140px;
  top: 30px;
  left: 30px;
  border-color: transparent #52737b transparent #52737b;
  -webkit-animation: lds-double-ring_reverse 2s linear infinite;
          animation: lds-double-ring_reverse 2s linear infinite; }

.spinner__double-ring {
  width: 73px !important;
  height: 73px !important;
  -webkit-transform: translate(-36.5px, -36.5px) scale(0.365) translate(36.5px, 36.5px);
          transform: translate(-36.5px, -36.5px) scale(0.365) translate(36.5px, 36.5px); }

.error-indicator {
  margin: 0 auto;
  max-width: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: darkgoldenrod; }

.error-indicator__title {
  font-size: 24px;
  font-weight: bold; }

.item-details {
    display: flex;
    margin-top: 1rem;
    flex-direction: row;
    padding: 14px;
    min-height: 240px;
}

.item-details .item-image {
    width: 30%;
    height: 30%;
    border-radius: 10px;
}

.item-details .list-group-item {
    padding: 6px;
}

.item-details .list-group-item .term{
    margin-right: 8px;
}

@media (min-width: 768px) {
    .item-details {
        margin-top: 0;
    }
}

.item-list .list-group-item {
    cursor: pointer;
}

.item-list .list-group-item:hover {
    background-color: #444;
}


.toggle-planet {
    margin-bottom: 14px;
}

